<template>
	<div id="popup" v-show="popupFlag">
		<div class="con" v-if="popupOneFlag">
			<img class="bgimg" src="../../assets/img/caidai01.png" />
			<div class="popup_one">
				<div v-show="copyFlag">
					<h3></h3>
					<p></p>
				</div>
				<div v-show="!copyFlag">
					<h3></h3>
				</div>
				<button type="button" 
			     v-clipboard:copy="message"
			     v-clipboard:success="onCopy"
			     v-clipboard:error="onError"
			     v-show="copyFlag"></button>
			     <div class="copySuccess"v-show="!copyFlag" style="width: 90%;margin: 0.2rem auto;">
			     	
			     </div>
			</div>
			<div class="close" @click="closePopup"></div>
		</div>
		<div class="con" v-if="popupTwoFlag">
			<div>
				<img class="bgimg" src="../../assets/img/caidai.png" />
				<div class="userImg">
					<img src="https://img.alicdn.com/tfscom/i1/2944846611/TB1FDdGckSWBuNjSszdXXbeSpXa_!!0-item_pic.jpg_q60" />
				</div>
			</div>
			<div class="popup_two">
				<div>
					<p><span>6.3</span></p>
					<p></p>
					<p>~</p>
					<button></button>
				</div>
			</div>
			<div class="close" @click="closePopup"></div>
		</div>
		<div class="con" v-if="popupSevenFlag">
			<div class="popup_two">
				<div class="download">
					<p>APP~</p>
					<p>APP</p>
					<p>0</p>
					<button @click="downloadApp"></button>
				</div>
			</div>
			<div class="close" @click="closePopup"></div>
		</div>
		<div class="con"  v-if="popupThreeFlag">
			<div class="popup_three">
				<h3></h3>
				<p>1.</p>
				<p>2.240</p>
				<p>3.</p>
				<p>4.</p>
			</div>
			<div class="close" @click="closePopup"></div>
		</div>
		<div class="con" v-if="popupFourFlag">
			<div class="popup_four">
				<h3></h3>
				<p>1.</p>
				<p>2.240</p>
				<p>3.</p>
				<p>4.</p>
			</div>
			<div class="close" @click="closePopup"></div>
		</div>
		<div class="con" v-if="popupFiveFlag">
			<div class="popup_five">
				<p>APP</p>
				<p>0</p>
				<button @click="downloadApp"></button>
			</div>
			<div class="close" @click="closePopup"></div>
		</div>
		<div class="con" v-if="popupSixFlag">
			<div class="popup_two">
				<div>
					<p style="margin-top:0.1rem ;">~</p>
					<p></p>
					<p>~</p>
					<button></button>
				</div>
			</div>
			<div class="close" @click="closePopup"></div>
		</div>
		<div class="con" v-if="popupEightFlag || popupNineFlag" style="top: 2rem;">
			<img class="bgimg" src="../../assets/img/caidai.png" />
			<img v-if="popupNineFlag" class="wx_jiantou" src="../../assets/img/wx_jiantou.png" />
			<div class="popup_eight" v-if="popupEightFlag">
				<div>
					<p><span>{{price}}</span></p>
					<p>0</p>
					<div class="weChat">
						<ul>
							<li @click="wexinShare(weixin)">
								<img src="../../assets/img/weChat.png" />
								<span></span>
							</li>
							<li @click="wexinShare(pengyouquan)">
								<img src="../../assets/img/pengyouquan.png" />
								<span></span>
							</li>
						</ul>
					</div>
				</div>
			</div>
			<div class="popup_eight" v-if="popupNineFlag">
				<div>
					<p style="margin-top: 0.575rem;"><span>{{price}}</span></p>
					<p style="margin-bottom: 0.575rem;">0</p>
				</div>
			</div>
			<div class="close" @click="closePopup"></div>
		</div>
	</div>
</template>

<script>
	import { Toast } from 'mint-ui';
	export default {
		name:'popup',
		props:["choice","message","price"],
		data () {
			return {
				popupFlag:false,//
				popupOneFlag:false,// 
				popupTwoFlag:false,//
				popupThreeFlag:false,//
				popupFourFlag:false,//
				popupFiveFlag:false,//app
				popupSixFlag:false,//
				popupSevenFlag:false,//   app
				popupEightFlag:false,//   
				popupNineFlag:false,//   
				copyFlag:true,//
			}
		},
		created () {
		},
		mounted () {
			
		},
		watch: {
			choice () {
				if(this.choice != '0'){
					this.popupFlag = true
					document.documentElement.style.overflow='hidden';
					document.body.style.overflow='hidden';//
					if(this.choice == 1){//1 2 3 4 5app 6 7   app 8    9   
						this.popupOneFlag = true
					}
					if(this.choice == 2){
						this.popupTwoFlag = true
					}
					if(this.choice == 3){
						this.popupThreeFlag = true
					}
					if(this.choice == 4){
						this.popupFourFlag = true
					}
					if(this.choice == 5){
						this.popupFiveFlag = true
					}
					if(this.choice == 6){
						this.popupSixFlag = true
					}
					if(this.choice == 7){
						this.popupSevenFlag = true
					}
					if(this.choice == 8){
						this.popupEightFlag = true;
					}
					if(this.choice == 9){
						this.popupNineFlag = true
					}
				}
			}
		},
		methods: {
			onCopy: function (e) {
		      console.log(': ' + e.text)
		      this.copyFlag = false;
		    },
		    onError: function (e) {
		      Toast({
				  message: '',
				  duration: 1500
				});
		    },
			closePopup () {
				this.popupFlag = false;
				this.copyFlag = true;
				this.popupOneFlag = false;
				this.popupThreeFlag = false;
				this.popupTwoFlag = false;
				document.documentElement.style.overflow='scroll';
				document.body.style.overflow='scroll';//
				this.$emit('reset', 0)
			},
			wexinShare (e){
				//app
				if(navigator.userAgent.match(/(iPhone|iPod|iPad);?/i)){
					//iphone
					if(e == 'weixin'){
						//
					}else if(e == 'pengyouquan'){
						//
					}
				}
				if(navigator.userAgent.match(/android/i)){
					//android
					if(e == 'weixin'){
						//
					}else if(e == 'pengyouquan'){
						//
					}
				}
			},
			downloadApp () {
				var _this = this;
				if(navigator.userAgent.match(/(iPhone|iPod|iPad);?/i)){
					console.log("iphone")
					window.location.href='https://itunes.apple.com/cn/app/id1391124408?mt=8';
				}
				if(navigator.userAgent.match(/android/i)){
//					_this.$router.push({name:'guideDownloadApp',params:{'token':'00000'}})
					if(navigator.userAgent.toLowerCase().match(/MicroMessenger/i) == "micromessenger"){
						//
						_this.$router.push({name:'guideDownloadApp',params:{'token':'00000'}})
					}else if (navigator.userAgent.toLowerCase().match(/WeiBo/i) == "weibo"){
						//
					}else if (navigator.userAgent.toLowerCase().match(/QQ/i) == "qq"){
						//qq
					}else{
						window.location.href='http://apitbk.726p.com/everydaysave_android.apk';
					}
				}
			}
		},
	}
</script>

<style scoped="scoped" lang="scss">
	.popup_one,.popup_two,.popup_three,.popup_four,.popup_five{
		position: relative;
		button{
			width: 1.415rem;
			height: 0.27rem;
			border-radius: 0.135rem;
			background: #FF2E2E;
			font-size: 0.14rem;
			color: #FFFFFF;
			border: none;
			display: block;
			margin: auto;
			margin-top: 0.3rem;
			margin-bottom: 0.23rem;
		}
	}
	#popup{
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,0.7);
		position: fixed;
		top: 0;
		left: 0;
		z-index: 2;
	}
	.con{
		width: 2.775rem;
		border-radius: 0.12rem;
		background: #FFFFFF;
		position: absolute;
		top: 1.5rem;
		left: 0;
		right: 0;
		margin: auto;
	}
	.popup_one{
		font-size: 0.15rem;
		color: #FF2E2E;
		line-height: 0.3rem;
		h3{
			margin-top: 0.3rem;
		}
		h3,p{
			text-align: center;
		}
		.copySuccess{
			margin-top: 0.3rem;
			margin-bottom: 0.23rem;
			color: #333333;
			font-size: 0.14rem;
			text-align: center;
			line-height: 0.27rem;
		}
	}
	.close{
		width: 0.34rem;
		height: 0.34rem;
		background: url(../../assets/img/close.png) no-repeat;
		background-size:100% 100%;
		position: absolute;
		bottom: -0.475rem;
		left: 0;
		right: 0;
		margin: auto;
	}
	.popup_two{
		p{
			text-align:center; 
			&:nth-of-type(1){
				font-size: 0.12rem;
				color: #333333;
				margin-top: 0.35rem;
				line-height: 0.3rem;
				span{
					color: #FF2E2E;
				}
			}
			&:nth-of-type(2),&:nth-of-type(3){
				color: #333333;
				font-size: 0.15rem;
				line-height: 0.3rem;
			}
		}
		button{
			margin-top: 0.225rem;
			-webkit-animation: start 1.5s infinite ease-in-out;
			box-shadow:0 0.08rem 0.13rem rgba(87,13,5,0.19);
		}
		.download{
			p:nth-of-type(1){
				margin-top: 0.12rem;
				margin-bottom: 0.1rem;
			}
		}
	}
	.popup_three,.popup_four{
		width: 2.66rem;
		margin: auto;
		margin-bottom: 0.16rem;
		h3{
			color: #333333;
			font-size: 0.16rem;
			margin-top: 0.15rem;
			padding-bottom: 0.08rem;
			border-bottom: 0.01rem solid #333333;
			text-align: center;
			margin-bottom: 0.11rem;
			font-weight: bold;
		}
		p{
			font-size: 0.13rem;
			color: #333333;
			margin-bottom: 0.13rem;
			line-height: 0.21rem;
		}
	}
	.popup_five{
		p{
			color: #333333;
			font-size: 0.16rem;
			line-height: 0.3rem;
			text-align: center;
			&:nth-of-type(1){
				margin-top: 0.2rem;
			}
		}
	}
	.popup_eight{
		position: relative;
		p{
			text-align: center;
			&:nth-of-type(1){
				color: #333333;
				font-size: 0.12rem;
				margin-top: 0.4rem;
				span{
					color: #FF2E2E;
				}
			}
			&:nth-of-type(2){
				font-size: 0.16rem;
				color: #FF2E2E;
				margin-top: 0.18rem;
			}
		}
		.weChat{
			padding: 0 0.2rem;
			margin-top:0.25rem ;
			margin-bottom:0.25rem ;
			ul{
				display: flex;
				justify-content: space-between;
				align-items: center;
			}
			li{
				flex: 1;
				text-align: center;
				img{
					width: 0.3rem;
					height: 0.3rem;
					margin: auto;
					margin-bottom:0.07rem ;
				}
				span{
					color: #333333;
					font-size: 0.11rem;
				}
			}
		}
	}
	.con{
		.wx_jiantou{
		    width: 0.6rem;
		    position: absolute;
		    top: -1.6rem;
		    right: -0.3rem;
		}
		.bgimg{
			position: absolute;
			width: 3.285rem;
			top: -0.575rem;
			left: -0.26rem;
		}
		.userImg{
			width: 0.52rem;
			height: 0.52rem;
			border-radius: 50%;
			border: 0.02rem solid #FFFFFF;
			overflow: hidden;
			position: absolute;
			left: 0;
			right: 0;
			top: -0.275rem;
			margin: auto;
			img{
				width: 100%;
				height: 100%;
			}
		}
	}
@-webkit-keyframes start {
	0% {transform: scale(0.9);}
	50% {transform: scale(1);}
	100% {transform: scale(0.9);}
}
@-moz-keyframes start {
	0% {transform: scale(0.9);}
	50% {transform: scale(1);}
	100% {transform: scale(0.9);}
}
@keyframes start {
	0% {transform: scale(0.9);}
	50% {transform: scale(1);}
	100% {transform: scale(0.9);}
}
</style>


