// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import axios from 'axios'
import { api } from './interface_api'
import VueAxios from 'vue-axios'
import Mint from 'mint-ui'
import 'mint-ui/lib/style.min.css'
Vue.use(VueAxios,axios,Mint)
import { Lazyload,Toast } from 'mint-ui';
Vue.use(Lazyload);
// 
axios.interceptors.request.use(function (config) {
  // 
  // config.headers['Content-Type'] = 'application/x-www-form-urlencoded'
  //loding
  let token = window.localStorage.getItem('token')
  let sex = window.localStorage.getItem('sex')
   // sourceheader
   config.headers['source'] = 6001
  return config
}, function (error) {
  // 
  console.log('')
  return Promise.reject(error)
})

//response
axios.interceptors.response.use((response) => {
  // 
//console.log(response)
  if (!response.data) {
    return Promise.reject(response)
  }
  return response
}, (error) => {
	//200
	if (error.toString().indexOf("timeout") != -1) {
	    console.log('')
	    Toast({
					  message: '',
					  duration: 3000
					});
	 }
	if(error && error.response){
		switch(error.response.status) {
			case 400:
		    	error.message = ''
		        break
		
		    case 401:
		      	error.message = ''
		        break
		
		    case 403:
		        error.message = ''
		        break
		
		    case 404:
//		        error.message = `: ${error.response.config.url}`
		        error.message = `404`
		        break
		        
		    case 405:
		        error.message = ''
		        break
		
		    case 408:
		        error.message = ''
		        break
		
		    case 500:
		        error.message = ''
		        break
		
		    case 501:
		        error.message = ''
		        break
		
		    case 502:
		        error.message = ''
		        break
		
		    case 503:
		        error.message = ''
		        break
		
		    case 504:
		        error.message = ''
		        break
		
		    case 505:
		        error.message = 'HTTP'
		        break
		
		    default:
		}
	}
	console.log(error.message)
	Toast({
				  message: error.message,
				  duration: 3000
				});
	return Promise.reject(error)
})

Vue.prototype.$axios = axios

//
Vue.prototype.URL = api
export{
	Vue
}




